<template>
    <div class="c-my-collections">
        <h3 class="c-sidebar-right-title">
            <i class="u-icon u-icon-mycollection"
                ><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M248.768 1.654L19.499 112.336a8.316 8.316 0 00-4.701 7.49v272.348a8.318 8.318 0 004.701 7.49l229.269 110.682a16.633 16.633 0 0014.464 0l229.269-110.682a8.316 8.316 0 004.701-7.49V119.826a8.318 8.318 0 00-4.701-7.49L263.232 1.654a16.64 16.64 0 00-14.464 0z" fill="#ffe17d"/><path d="M16.181 115.273a8.286 8.286 0 00-1.383 4.553v272.348a8.318 8.318 0 004.701 7.49l229.269 110.681a16.624 16.624 0 007.232 1.654V231.048L16.181 115.273z" fill="#ffc350"/><path d="M495.819 115.273a8.286 8.286 0 011.383 4.553v272.348a8.318 8.318 0 01-4.701 7.49L263.232 510.345A16.625 16.625 0 01256 512V231.048l239.819-115.775z" fill="#ffd164"/><path d="M83.407 377.657a8.298 8.298 0 01-3.606-.828L44.404 359.74a8.31 8.31 0 01-3.874-11.103c1.99-4.134 6.977-5.856 11.103-3.874l35.397 17.09a8.31 8.31 0 013.874 11.103c-1.429 2.971-4.402 4.701-7.497 4.701z" fill="#ffe17d"/><path d="M115.986 356.45a8.298 8.298 0 01-3.606-.828l-67.928-32.79c-4.134-1.998-5.872-6.969-3.874-11.103 1.99-4.126 6.985-5.856 11.103-3.874l67.927 32.79c4.134 1.998 5.872 6.969 3.874 11.103-1.428 2.971-4.401 4.702-7.496 4.702z" fill="#fff6d8"/><path fill="#ff8087" d="M414.549 154.373L173.887 38.11l-63.44 30.75 241.202 116.443z"/><path d="M351.649 185.303v90.207c0 3.066 3.205 5.078 5.967 3.745l52.232-25.215a8.316 8.316 0 004.701-7.49v-92.176l-62.9 30.929z" fill="#e6646e"/></svg></i>
            <span>我的小册</span>
            <a
                class="fr el-button el-button--success el-button--mini"
                :href="publish_url('collection')"
            >
                <i class="el-icon-document-add"></i>
                <span>创建</span>
            </a>
        </h3>
        <div class="m-my-collections">
            <template v-if="data && data.length">
                <router-link
                    class="m-my-collection"
                    v-for="(collection, key) in $store.state.my_collections"
                    :key="key"
                    :to="{
                        name: 'view',
                        params: { collection_id: collection.id },
                    }"
                >
                    <h5 class="u-title" v-text="collection.title"></h5>
                    <div class="u-misc">
                        <div @click="delete_handle($event, collection.id)">
                            <i class="el-icon-delete u-delete" title="删除"></i>
                        </div>
                        <div @click="edit_handle($event, collection.id)">
                            <i class="el-icon-edit u-edit" title="编辑"></i>
                        </div>
                        <span
                            class="u-updated"
                            v-text="
                                '编辑于 ' +
                                    $options.filters.date_format(
                                        collection.updated
                                    )
                            "
                        ></span>
                    </div>
                </router-link>
                <a
                    class="u-more"
                    href="/dashboard/#/other?type=collection"
                    target="_blank"
                    >查看更多 &raquo;</a
                >
            </template>
            <div class="u-tip" v-else-if="user.uid">
                <i class="el-icon-warning-outline"></i> 暂无剑三小册记录
            </div>
            <div class="u-tip" v-else>
                <i class="el-icon-warning-outline"></i> 请先进行登录
            </div>
        </div>
    </div>
</template>

<script>
import User from "@jx3box/jx3box-common/js/user.js";
import { get_my_collections, remove_collection } from "../service/collection";
import publish_url from "../filters/PublishUrl";

export default {
    name: "MyCollections",
    data() {
        return {
            user: User.getInfo(),
            limit: 5,
        };
    },
    computed: {
        data: function() {
            return this.$store.state.my_collections;
        },
    },
    methods: {
        publish_url,
        edit_handle($event, collection_id) {
            $event.preventDefault();
            location.href = this.publish_url(`collection/${collection_id}`);
            return false;
        },
        delete_handle($event, collection_id) {
            $event.preventDefault();
            this.$confirm("确认是否删除该剑三小册？", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning",
            }).then(() => {
                remove_collection(collection_id).then((data) => {
                    data = data.data;
                    if (data.code === 200) {
                        this.$message.success(data.message);
                        // 获取我的小册
                        get_my_collections({ limit: this.limit });
                    } else {
                        this.$message.error(data.message);
                    }
                });
            });
            return false;
        },
    },
    mounted() {
        // 获取我的小册
        get_my_collections({ limit: this.limit });
    },
};
</script>

<style lang="less">
@import "../assets/css/components/my_collections.less";
</style>
